
import { Component, Prop, Ref, Vue } from "vue-property-decorator";

@Component({
  name: "WatWaterMark",
  components: {},
})
export default class WatWaterMark extends Vue {
  @Prop({ default: "" }) private text!: string;
  @Prop({ default: "" }) private overlapStyle!: string;
  @Prop({ default: 120 }) private width!: number;
  @Prop({ default: 160 }) private height!: number;
  @Prop({ default: 36 }) private fontSize!: number;
  @Prop({ default: "#f9f9fa" }) private fontColor!: string;
  @Prop({ default: -22 }) private rotate!: number;

  @Ref("canvas") readonly canvas!: HTMLCanvasElement;

  private base64Url = "";

  mounted() {
    this.$nextTick(() => this.draw());
  }

  private draw() {
    const { text, height, fontSize, fontColor, rotate } = this;
    const ctx = this.canvas.getContext("2d");
    if (ctx) {
      ctx.font = `bold ${fontSize}px Arial`;
      ctx.fillStyle = fontColor;
      ctx.rotate((rotate * Math.PI) / 180);
      ctx.fillText(text, 0, height);
      this.base64Url = this.canvas.toDataURL("image/png");
    }
  }
}
