import {
  IDragQuestion,
  IDragQuestionData,
  IQuestionComponents,
  ILabelQuestionBlankViews,
  IQuestion,
  IQuestionData,
  IQuestionOptions,
  ITagQuestion,
  ITagRect,
} from "@/store/types";
import axios from "axios";
import xmldoc from "xmldoc";
import _ from "lodash";
import { LruCache } from "./lru";

const cache = new LruCache<string>(50);
const regNumericValues =
  /^([-+]?\d*\.?\d+([eE][-+]?\d+)?)(px|pt|pc|mm|cm|m|in|ft|em|ex|%)?$/;

export const fetchSvg = async (url: string) => {
  try {
    const value = cache.get(url);
    if (value == undefined) {
      const res = await axios.get(url);
      if (res.data) {
        cache.set(url, res.data);
        return res.data;
      }
    }
    return value;
  } catch (error) {
    console.error(error);
  }
};

const getElementsByName = (
  document: xmldoc.XmlDocument,
  name: string
): xmldoc.XmlElement[] => {
  const matches: xmldoc.XmlElement[] = [];
  for (let i = 0; i < document.children.length; i++) {
    const element = document.children[i];
    if (
      element instanceof xmldoc.XmlElement &&
      element.name == name &&
      element.attr["id"].indexOf("item") != -1
    ) {
      matches.push(element);
    }
  }
  return matches;
};

/// 
const removeUints = (document: xmldoc.XmlDocument) => {
  const remove = (name: string) => {
    if (document.attr[name]) {
      const match = document.attr[name].match(regNumericValues);
      if (match) document.attr[name] = match[1];
    }
  };
  remove("width");
  remove("height");
  return document;
};

// 
const castTagQuestion = async (
  question: IQuestionData
): Promise<ITagQuestion> => {
  const tagRects: ITagRect[] = [];
  const labels: ILabelQuestionBlankViews[] = [];
  const options: IQuestionOptions[] = [];
  let rawSvg;
  if (question.ReferenceUrl) {
    rawSvg = await fetchSvg(question.ReferenceUrl);
    if (rawSvg) {
      let document = new xmldoc.XmlDocument(rawSvg);
      document = removeUints(document);
      const rects = getElementsByName(document, "rect");
      for (let i = 0; i < question.OptionGroups.length; i++) {
        const group = question.OptionGroups[i];
        const filterLabels = _.filter(
          group.LabelQuestionBlankViews,
          (label) => label.Act == "click"
        );
        labels.push(...filterLabels);
        if (group.QuestionOptions) {
          options.push(...group.QuestionOptions);
        }
      }

      for (let i = 0; i < labels.length; i++) {
        const rect = _.find(
          rects,
          (value) => value.attr["name"] == labels[i].NameInSvg
        );
        if (rect) {
          tagRects.push({
            id: rect.attr["id"],
            name: rect.attr["name"],
            dx: Number(rect.attr["x"]),
            dy: Number(rect.attr["y"]),
            width: Number(rect.attr["width"]),
            height: Number(rect.attr["height"]),
          });
        }
      }

      //  rect 
      _.remove(
        document.children,
        (element) =>
          element instanceof xmldoc.XmlElement &&
          element.name == "rect" &&
          element.attr["id"].indexOf("item") != -1
      );

      //  line 
      _.remove(
        document.children,
        (element) =>
          element instanceof xmldoc.XmlElement &&
          element.name == "line" &&
          _.findIndex(
            labels,
            (label) => label.NameInSvg == element.attr["name"]
          ) == -1
      );

      rawSvg = document.toString();
    }
  }

  return {
    type: "tag",
    questionId: question.QuestionId,
    rawSvg: rawSvg ?? "",
    rects: tagRects,
    labels: labels,
    options: options,
  };
};

// 
const castDragQuestion = async (
  question: IQuestionData
): Promise<IDragQuestion> => {
  const components: IQuestionComponents[] = [];
  if (question.OptionGroups.length > 0) {
    const content = question.OptionGroups[0].QuestionBody;
    if (content) {
      const dragQuestion: IDragQuestionData = JSON.parse(content);
      components.push(...dragQuestion.questionComponents);
    }
  }

  for (let i = 0; i < components.length; i++) {
    const component = components[i];
    if (!component.rawSvg) {
      let rawSvg = await fetchSvg(component.url);
      let document = new xmldoc.XmlDocument(rawSvg);
      document = removeUints(document);
      rawSvg = document.toString();
      component.rawSvg = rawSvg;
    }
  }

  return {
    type: "drag",
    questionId: question.QuestionId,
    content: question.QuestionContent,
    components: components,
  };
};

export const castQuestions = async (
  questions: IQuestionData[]
): Promise<IQuestion[]> => {
  const results: IQuestion[] = [];
  for (let i = 0; i < questions.length; i++) {
    const question = questions[i];
    if (question.QuestionTypeId == 8 || question.QuestionTypeId == 9) {
      // 8  9 
      results.push(await castTagQuestion(question));
    } else if (question.QuestionTypeId == 10) {
      // 10 
      results.push(await castDragQuestion(question));
    }
  }
  return results;
};
