
export interface ChoiceModel {
  id: string;
  value: string;
}

export type Choice = ChoiceModel;

import { Component, Vue, Prop, Emit } from "vue-property-decorator";

@Component({
  name: "QuestionItem",
  components: {},
})
export default class QuestionItem extends Vue {
  public selected = "";

  @Prop({ required: true })
  options!: ChoiceModel[];

  @Prop({ default: "" })
  selectIndex!: string;

  @Emit()
  tap(value: any) {
    this.selected = value.id;
    this.$emit("input", this.selected);
  }

  mounted() {
    this.selected = this.selectIndex;
  }
}
