
import { Component, Vue } from "vue-property-decorator";
import { QuestionsModule } from "@/store/modules/questions";

@Component({
  name: "BottomView",
  components: {},
})
export default class BottomView extends Vue {
  get isFirst() {
    return QuestionsModule.isFirst;
  }

  get isLast() {
    return QuestionsModule.isLast;
  }

  private previous() {
    QuestionsModule.previous();
  }

  private next() {
    QuestionsModule.next();
  }
}
