
import { QuestionsModule } from "@/store/modules/questions";
import { Component, Vue, Ref } from "vue-property-decorator";
import WatWaterMark from "@/components/WatWaterMark.vue";
import AnswerSheet from "./components/AnswerSheet/AnswerSheet.vue";
import BottomView from "./components/BottomView/BottomView.vue";
import NavView from "./components/NavView/NavView.vue";
import ChoiceQuestion from "./components/ChoiceQuestion/ChoiceQuestion.vue";
import TagQuestion from "./components/TagQuestion/TagQuestion.vue";
import DragQuestion from "./components/DragQuestion/DragQuestion.vue";
import UserInfo from "./components/UserInfo/UserInfo.vue";
import { ElMain } from "element-ui/types/main";

@Component({
  name: "QuestionsView",
  components: {
    WatWaterMark,
    ChoiceQuestion,
    TagQuestion,
    DragQuestion,
    UserInfo,
    AnswerSheet,
    BottomView,
    NavView,
  },
})
export default class QuestionsView extends Vue {
  @Ref("main") readonly main!: ElMain;

  async mounted() {
    // console.log("mounted", this.main.$el.getBoundingClientRect());
    await QuestionsModule.fetchQuestions();
    window.addEventListener("resize", this.handleResize);
  }

  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  }

  get question() {
    return QuestionsModule.question;
  }

  private handleResize() {
    console.log(this.main.$el.getBoundingClientRect());
  }
}
