import {
  VuexModule,
  Module,
  Action,
  Mutation,
  getModule,
} from "vuex-module-decorators";
import store from "@/store";
import { getQuestionIndex, setQuestionIndex } from "@/utils/cookies";
import { IQuestion } from "../types";
import { TestQuestions } from "./questions-data";
import { castQuestions } from "@/utils/question";

export interface IQuestionsState {
  index: number; // 
  questionList: IQuestion[];
}

@Module({ dynamic: true, store, name: "questions" })
class Questions extends VuexModule implements IQuestionsState {
  public index = Number(getQuestionIndex() || "0");
  public questionList: IQuestion[] = [];

  get question() {
    return this.questionList[this.index];
  }

  get isFirst() {
    return this.index == 0;
  }

  get isLast() {
    return this.index == this.questionList.length - 1;
  }

  @Mutation
  private SET_INDEX(index: number) {
    this.index = index;
    setQuestionIndex(`${index}`);
  }

  @Mutation
  private SET_QUESTIONS(questions: IQuestion[]) {
    this.questionList = questions;
  }

  @Action
  public async fetchQuestions() {
    const questions = await castQuestions(TestQuestions);
    this.SET_QUESTIONS(questions);
  }

  @Action
  public setIndex(index: number) {
    if (index < 0) index = 0;
    if (index > this.questionList.length - 1)
      index = this.questionList.length - 1;
    this.SET_INDEX(index);
  }

  @Action
  public previous() {
    if (this.isFirst) return;
    const index = this.index - 1;
    this.SET_INDEX(index);
  }

  @Action
  public next() {
    if (this.isLast) return;
    const index = this.index + 1;
    this.SET_INDEX(index);
  }

  @Action
  public questionNumberTap(value: any) {
    console.log(value);
  }

  @Action
  public async submit() {
    console.log("");
  }
}

export const QuestionsModule = getModule(Questions);
