
import { Component, Emit, Vue, Prop } from "vue-property-decorator";
import { QuestionsModule } from "@/store/modules/questions";

@Component({
  name: "QuestionNumber",
  components: {},
})
export default class QuestionNumber extends Vue {
  @Prop({ required: true })
  title!: string;

  @Prop({ required: true })
  items!: [];

  private questionModule = QuestionsModule;

  @Emit()
  tap(value: any) {
    this.questionModule.questionNumberTap(value);
  }
}
