
import { Component, Emit, Vue, Prop, Model } from "vue-property-decorator";

@Component({
  name: "radio",
  components: {},
})
export default class Radio extends Vue {
  @Prop({ default: "radio_id" })
  id!: string;

  @Prop()
  name!: string;

  @Prop()
  value!: string;

  @Prop()
  modelVale!: any | undefined;

  @Prop()
  className!: string;

  @Prop({ default: false })
  checked!: boolean;

  @Prop({ default: false })
  required!: boolean;

  @Prop({ default: false })
  disabled!: boolean;

  @Prop()
  model!: any;

  @Model("input") modelValue!: any;

  get state() {
    if (this.modelValue === undefined) {
      return this.checked;
    }
    return this.modelValue === this.value;
  }

  @Emit()
  onChange() {
    this.toggle();
  }

  @Emit()
  toggle() {
    this.$emit("input", this.state ? "" : this.value);
  }
}
