
import QuestionNumber from "./QuestionNumber.vue";
import Radio from "./Radio.vue";
import QuestionState from "./QuestionState.vue";
import { Component, Emit, Vue, Watch } from "vue-property-decorator";
import { QuestionsModule } from "@/store/modules/questions";

@Component({
  name: "AnswerSheet",
  components: {
    Radio,
    QuestionState,
    QuestionNumber,
  },
})
export default class AnswerSheet extends Vue {
  public selectIndex = "0";
  private questionsModule = QuestionsModule;

  @Emit()
  submit() {
    this.questionsModule.submit();
  }

  @Watch("selectIndex")
  onValueChanged() {
    console.log("" + this.selectIndex);
  }

  async mounted() {
    console.log("mounted");
  }
}
